import React, { useState } from 'react';
import { observer } from "mobx-react";

import ReceiptForm from '../../components/receiptform/ReceiptForm';
import './NewEditReceipt.css'

const NewEditReceipt = observer((props) => {

  const receiptStore = props.receiptStore;
  const payeeStore = props.payeeStore;

  let isEditing = false;
  let identifier = null;
  if (props.receiptId) {
    isEditing = true;
    identifier = props.receiptId;
  } else {
    identifier = Date.now();
    receiptStore.add({
      id: identifier,
      paidValue: "",
      payer: "",
      payerAddress: "",
      referrerTo: ""
    });
  }
  
  const receipt = receiptStore.receipts[identifier];

  const cancel = function() {
    receiptStore.removeById(identifier);
    window.location.hash = "#receipt-list";
  };

  const done = function() {
    window.location.hash = "#receipt-list";
  };

  return (
    <div className="new-receipt">
      <header className="top-app-bar">
        <div className="top-app-bar__navigation">
          <a onClick={() => (window.history.back())}>
            <i className="fas fa-arrow-left"></i>
          </a>
        </div>

        <div className="top-app-bar__title">
          Recibo
        </div>

        {!isEditing && 
        <div className="top-app-bar__right-part">
          <a onClick={() => cancel()}>
            <i class="fas fa-times top-app-bar__icon"></i>
          </a>
        </div>}
      </header>

      <ReceiptForm
        key={receipt.id}
        index={receipt.id}
        payee={payeeStore.payee.name}
        payeeCpfOrRg={payeeStore.payee.cpfOrRg}
        payeeAddress={payeeStore.payee.address}
        date={payeeStore.payee.date}
        receipt={receipt}/>

      <div className="action-bar">
        <button onClick={() => done()}>
          Pronto
        </button>
      </div>
    </div>
  );
})
export default NewEditReceipt;