import React, { useState } from 'react';
import { observer } from "mobx-react";
import ReceiptCard from '../../components/receiptcard/ReceiptCard';

import './ReceiptCards.css';

const ReceiptCards = observer((props) => {
  const receiptStore = props.receiptStore;
  const payeeStore = props.payeeStore;
  
  const[payerNameFiltering, setPayerNameFiltering] = useState("");

  function removeReceipt(id) {
    receiptStore.removeById(id);
  }

  return (
    <div className="receipts-card">
      
      <header className="top-app-bar">
        <div className="top-app-bar__navigation">
          <a>
            <i class="fas fa-home"></i>
          </a>
        </div>

        <div className="top-app-bar__title">
          Recibos Facil
        </div>
        
        <div className="top-app-bar__right-part">
          <input 
              
              type="text"   
              name="search" 
              placeholder="Pesquise por nome" 
              id="search"
              class="search"
              value={payerNameFiltering} onChange={(e) => setPayerNameFiltering(e.target.value)}
              />
        </div>
      </header>

      <div className="receipts-card-container">

      {Object.keys(receiptStore.receipts).map((id) => {
        const receipt = receiptStore.receipts[id];
        
        if (!receipt.payer.includes(payerNameFiltering)) {
          return
        }

        return <ReceiptCard
            key={id}
            index={id}
            payee={payeeStore.payee.name}
            payeeCpfOrRg={payeeStore.payee.cpfOrRg}
            payeeAddress={payeeStore.payee.address}
            date={payeeStore.payee.date}
            receipt={receipt}

            removeReceipt={removeReceipt}
          />
      })} 
      
      </div>
    </div>
  );
})

export default ReceiptCards;